import { TitleSection } from "../TitleSection/TitleSection";
import { Chart } from "./Chart";
import { Section, Subtitle } from "./ChartSection-styled";

export const ChartSection = ({ membersArray }) => {
  const optionsByType = [
    "",
    "",
    " ",
    "",
    "",
    " ",
    " ",
  ];
  const optionsBySex = ["male", "female"];
  const optionsByForhand = ["", ""];
  const optionsByBackhand = [
    "",
    "  ",
    "",
  ];

  const optionsByHomeTown = ["", ""];

  const optionsByAge = [
    " 20 ",
    "20-40 ",
    "40-60 ",
    " 60 ",
  ];

  const optionsByExperience = [
    " 2 ",
    "2-5 ",
    "5-10 ",
    " 10 ",
  ];

  return (
    <>
      <Section>
        <TitleSection icon={"#icon-chart"} title={" :"} />
        <Subtitle> :</Subtitle>
        <Chart
          membersArray={membersArray}
          options={optionsByType}
          type={"type"}
        />

        <Subtitle> :</Subtitle>
        <Chart
          membersArray={membersArray}
          options={optionsBySex}
          type={"sex"}
        />

        <Subtitle> :</Subtitle>
        <Chart
          membersArray={membersArray}
          options={optionsByForhand}
          type={"forhand"}
        />

        <Subtitle> :</Subtitle>
        <Chart
          membersArray={membersArray}
          options={optionsByBackhand}
          type={"backhand"}
        />

        <Subtitle>  :</Subtitle>
        <Chart
          membersArray={membersArray}
          options={optionsByHomeTown}
          type={"hometown"}
        />

        <Subtitle> :</Subtitle>
        <Chart
          membersArray={membersArray}
          options={optionsByAge}
          type={"birthDate"}
        />

        <Subtitle> :</Subtitle>
        <Chart
          membersArray={membersArray}
          options={optionsByExperience}
          type={"joinTennisYear"}
        />
      </Section>
    </>
  );
};
