import { List, Section, SectionWrap, TitleH2 } from "./AboutSection-styled";

import { AboutItem } from "./AboutItem";

export const AboutSection = () => {
  const aboutList = [
    {
      id: "about-1",
      icon: "#icon-serve",
      title: "   ",
      text: "   -            .",
    },
    {
      id: "about-2",
      icon: "#icon-racket",
      title: " ",
      text: "      ',   ,     ,        .",
    },
    {
      id: "about-3",
      icon: "#icon-award",
      title: "  ",
      text: "         .    ,          100.",
    },
    {
      id: "about-4",
      icon: "#icon-girl",
      title: " ",
      text: "    ,    - .     ,              .",
    },
  ];

  return (
    <>
      <Section>
        <SectionWrap>
          <TitleH2>  :</TitleH2>
          <List>
            {aboutList.map((item, index) => (
              <AboutItem key={item.id} item={item} index={index} />
            ))}
          </List>
        </SectionWrap>
      </Section>
    </>
  );
};
