import styled from "styled-components";

export const Section = styled.section`
  padding-bottom: 50px;
  min-height: 800px;
  background-color: var(--secondary-white-color);
`;

export const List = styled.ul``;

export const Item = styled.li`
  position: relative;
  overflow: hidden;
  width: 100%;
  height: 50px;

  &:nth-child(odd) {
    background-color: var(--primary-white-color);
  }

  &:nth-child(even) {
    background-color: var(--secondary-white-color);
  }
`;

export const ItemIndictator = styled.div`
  position: absolute;
  left: 0;
  width: 7px;
  height: 100%;
  opacity: 1;
  border-top-right-radius: 7px;
  border-bottom-right-radius: 7px;
`;

export const ItemIndictatorBG = styled.div`
  position: absolute;
  right: 0;

  width: 50%;
  height: 65%;
  opacity: 0.4;
`;

export const ItemWrap = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;

  padding-left: 24px;
  padding-right: 24px;
  margin-left: auto;
  margin-right: auto;
  max-width: 1200px;
  width: 100%;
  height: 100%;
`;

export const ItemText = styled.p`
  overflow: hidden;
  display: flex;
  justify-content: left;
  align-items: center;
  height: 100%;
  gap: 15px;

  font-size: 16px;
  font-weight: 400;

  &:nth-child(1) {
    min-width: 100px;
    flex-grow: 1;
  }

  &:nth-child(2) {
    min-width: 100px;
    justify-content: right;
  }
`;

export const Heading = styled.div`
  position: relative;
  overflow: hidden;
  width: 100%;
  height: 100px;
  background-color: var(--list-heading-bg-color);
  color: var(--primary-black-color);
`;

export const HeadingWrap = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;

  padding-left: 24px;
  padding-right: 24px;
  margin-left: auto;
  margin-right: auto;
  max-width: 1200px;
  width: 100%;
  height: 100%;
`;

export const HeadingText = styled.div`
  overflow: hidden;
  display: flex;
  justify-content: left;
  align-items: center;
  height: 100%;

  font-size: 16px;
  font-weight: 400;
  line-height: 1.6;

  &:nth-child(1) {
    width: 35px;
  }

  &:nth-child(2) {
    min-width: 100px;
    flex-grow: 1;
  }

  &:nth-child(3) {
    min-width: 50px;
    width: 10%;
    justify-content: center;
    border-right: 1px solid var(--primary-black-color);
  }
  &:nth-child(4) {
    min-width: 50px;
    width: 10%;
    justify-content: center;
  }
  &:nth-child(5) {
    min-width: 50px;
    width: 10%;
    justify-content: right;
  }
`;

export const Button = styled.button`
  display: inline-flex;
  justify-content: center;
  align-items: center;
  gap: 10px;

  flex-shrink: 0;

  height: 50px;
  padding: 12px;

  border-radius: 50px;
  background-color: transparent;
  border: 2px solid var(--primary-white-color);
  color: var(--primary-white-color);

  font-size: 14px;
  font-weight: 400;

  transition: 250ms cubic-bezier(0.4, 0, 0.2, 1);

  &:hover {
    border: 2px solid var(--accent-hover-color);
    color: var(--accent-hover-color);
  }
`;

export const ButtonIconSvg = styled.svg`
  width: 20px;
  height: 20px;
  fill: currentColor;
`;
