import {
  Button,
  ButtonIconSvg,
  ButtonLink,
  ButtonWrap,
  HeroButtonsWrap,
  IconSvg,
  IconSvgWrap,
  Section,
  SectionWrap,
  SubTitle,
  TitleH1,
  LogoIconWrap,
  TitleH1Text,
  HeartIconSvgWrap,
  TitleH2,
  TitleH2Text,
  FirstPoster,
  SecondPoster,
} from "./HeroSection-styled";

import PosterImg5 from "./img/poster5.jpg";
import PosterImg6 from "./img/poster6.jpg";

import sprite from "../../sprite.svg";

export const HeroSection = () => {
  return (
    <>
      <Section>
        <FirstPoster style={{ backgroundImage: `url(${PosterImg6})` }} />
        <SecondPoster style={{ backgroundImage: `url(${PosterImg5})` }} />
        <SectionWrap>
          <TitleH2>
            <HeartIconSvgWrap>
              <IconSvg>
                <use href={sprite + "#icon-ukr-heart"}></use>
              </IconSvg>
            </HeartIconSvgWrap>
            <TitleH2Text> </TitleH2Text>
          </TitleH2>

          <TitleH1>
            <TitleH1Text></TitleH1Text>
            <LogoIconWrap>
              <IconSvg>
                <use href={sprite + "#logo-100"}></use>
              </IconSvg>
            </LogoIconWrap>
          </TitleH1>
          <SubTitle>
               "-100"    .
          </SubTitle>
          <SubTitle>
                 -    , 
               4- .     : ,
            , ;       
                     .
          </SubTitle>

          <HeroButtonsWrap>
            <ButtonLink to="/gallery">
              <ButtonIconSvg>
                <use href={sprite + "#icon-camera"}></use>
              </ButtonIconSvg>
                
            </ButtonLink>
            <ButtonLink to="/members">
              <ButtonIconSvg>
                <use href={sprite + "#icon-users"}></use>
              </ButtonIconSvg>
               
            </ButtonLink>
            <ButtonWrap>
              <Button
                href="https://send.monobank.ua/jar/3MpUJRH8gs"
                rel="noopener noreferrer"
                aria-label="     "
              >
                <ButtonIconSvg>
                  <use href={sprite + "#icon-credit-card"}></use>
                </ButtonIconSvg>
                  
              </Button>
              <IconSvgWrap>
                <IconSvg>
                  <use href={sprite + "#icon-mono"}></use>
                </IconSvg>
              </IconSvgWrap>
            </ButtonWrap>
          </HeroButtonsWrap>
        </SectionWrap>
      </Section>
    </>
  );
};
